<template>
  <div id="app">
    <div class="content2" v-show='isshow'>
        <div class="add">
            <div class="header">
                <span style="float: left;">新增案例库</span>
                <span style="float: right; cursor: pointer;"  @click='show'>X</span>
            </div>
            <div class="zhong" style="margin-top: 50px; margin-left: 100px; text-align: left;  width: 500px;">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                            <el-form-item  label="电厂名称" >
                              <el-input style="width: 350px;" v-model="form.value" placeholder="电厂名称" id="btn0"></el-input>
                            </el-form-item>
                 </el-form>
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item  label="电厂名称" >
                                <el-row>
                                        <el-button type="primary">上传附件</el-button>
                                </el-row>
                        </el-form-item>
             </el-form>
            </div>
            <div class="bottom" style="text-align: center; margin-top: 30px;" >
                    <el-row>
                            <el-button type="primary"> <router-link to='./Afteradd'> 保存</router-link></el-button>
                    </el-row>
            </div>
        </div>
    </div>
    <div class='content'>
        <div class="center">
            <div class="img">
                <img src="../../../src/assets/img/img2.jpg" alt="" width="80px" height="80px">
                <el-row>
                        <el-button type="primary"@click='show' >新增案例库</el-button>
                </el-row>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Addanli',
    data() {
      return {
        isshow:false,
        form:{
            value:''
        },
      }
    },
    methods:{
        show(){
            this.isshow = !this.isshow
        }
    },
    activated(){
      this.$router.push(this.path);   //处理默认现实的路由
    },
    beforeRouteLeave(to, from, next){
      this.path = this.$route.path;
      next();
    },
  }

</script>

<style lang="" scoped>
  .content {
    position: absolute;
    width: 97%;
    height: 85%;
    left:1.5%;
    background: white;
  }
 
  .center{
      position: absolute;
      width: 150px;
      height: 150px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      /* border: 2px solid red; */
      text-align: center;
  }

  .el-button{
      padding: 3px;
      width: 80px;
      height:30px;
      /* text-align: center; */
      /* line-height: px; */
  }

  .content2{
      position: absolute;
      width: 100%;
      height: 87%;
      /* background: black; */
      background-color:rgba(0,0,0,0.5);
      z-index: 2;
      
  }

  .add{
    position: absolute;
      width: 650px;
      height: 260px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 15px;
      background: white;
  }
</style>



